import React, { useEffect, useState, useRef } from 'react';
import './Map.css';
import defaultImage from '../assets/dafault-place.png'; //     

// InfoWindow      (  )
export const infoWindowRef = { current: null };

export async function openInfoWindow({ map, marker, lat, lng, address }) {
  const place = await getPlaceNameFromCoords(lat, lng);
  const infoWindow = new window.naver.maps.InfoWindow({
    content: `
      <div style="padding: 10px; min-width: 200px;">
        <p>: ${lat.toFixed(6)}</p>
        <p>: ${lng.toFixed(6)}</p>
        <p>: ${(place || address)}</p>
      </div>
    `,
    borderColor: '#ccc',
    borderWidth: 1,
    anchorSize: new window.naver.maps.Size(10, 10)
  });
  infoWindow.open(map, marker);
  infoWindowRef.current = infoWindow;
}

const searchNaverPlace = async (query) => {
  try {
    const response = await fetch(`http://localhost:5001/api/search?query=${encodeURIComponent(query)}`);
    const data = await response.json();
    console.log(data); //  
    return data;
  } catch(err) {
    return null;
  }
};

//    
const getPlaceNameFromCoords = async (lat, lng) => {
  try {
    const response = await fetch(`http://localhost:5001/api/poi?lat=${lat}&lng=${lng}`);
    const data = await response.json();
    if (data.documents && data.documents.length > 0) {
      const nearest = getNearestPlace(lat, lng, data.documents);
      return nearest.place_name;
    } else {
      return null;
    }
  } catch (err) {
    console.error('   :', err);
    return null;
  }
};

//    
const getNearestPlace = (lat, lng, items) => {
  if (!items || items.length === 0) return null;
  return items.sort((a, b) => a.distance - b.distance)[0]; //   1
};

//      
export async function showRouteBetweenPoints({ startLat, startLng, endLat, endLng, map }) {
  const routeData = await fetchNaverRoute(startLat, startLng, endLat, endLng);
  if (routeData) {
    return drawRouteOnMap(map.current, routeData);
  }
}

// pathData  
async function fetchNaverRoute(startLat, startLng, endLat, endLng) {
  const start = `${startLng},${startLat}`;  //  "lng,lat" 
  const goal = `${endLng},${endLat}`;

  try {
    const response = await fetch(`http://localhost:5001/api/route?start=${start}&goal=${goal}`);
    const data = await response.json();
    return data;
  } catch (err) {
    console.error(' API  :', err);
    return null;
  }
}

// pathData    
function drawRouteOnMap(map, pathData) {
  if (!pathData?.route?.traoptimal?.[0]?.path) {
    console.log('   .');
    return;
  }

  const rawPath = pathData.route.traoptimal[0].path;
  const convertedPath = rawPath.map(([lng, lat]) => new window.naver.maps.LatLng(lat, lng));

  const polyline = new window.naver.maps.Polyline({
    map: map,
    path: convertedPath,
    strokeColor: '#007BFF',
    strokeWeight: 5
  });
  const arrowMarkers = addArrowMarkers(convertedPath, map);
  return [polyline, arrowMarkers];
}

//      
function addArrowMarkers(path, map) {
  let markers = [];
  const interval = 10;
  for (let i = interval; i < path.length; i+=interval) {
    const prev = path[i-1];
    const curr = path[i];
  
    const dx = curr.lng() - prev.lng();
    const dy = curr.lat() - prev.lat();
    const angle = Math.atan2(dx, dy) * 180 / Math.PI - 90;
  
    const midLat = (prev.lat() + curr.lat()) / 2;
    const midLng = (prev.lng() + curr.lng()) / 2;
  
    const newMarker = new window.naver.maps.Marker({
      position: new window.naver.maps.LatLng(midLat, midLng),
      map,
      icon: {
        content: `<div style="
          transform: rotate(${angle}deg);
          font-size: 20px;
          color: red;
        "></div>`,
        anchor: new window.naver.maps.Point(8, 8)
      }
    });
    markers.push(newMarker)
  }
  return markers;
}

//  :   
export function removeCurrentMarker(markerRef) {
  if (markerRef.current) {
    markerRef.current.setMap(null);
  }
}

//  :     
function createAndRegisterMarker(position, mapRef, markerRef) {
  const marker = new window.naver.maps.Marker({
    position,
    map: mapRef.current
  });
  markerRef.current = marker;
  return marker;
}

//  :    
function updateMarkerInfo(setMarkerInfo, lat, lng, address) {
  setMarkerInfo({ lat, lng, address });
}

export function getAddress(address) {
  return address.roadAddress || address.jibunAddress;
}

function Map() {
  //  
  const [markerInfo, setMarkerInfo] = useState(null);
  const [showInput, setShowInput] = useState(false);
  const [placeName, setPlaceName] = useState('');
  const [placeDescription, setPlaceDescription] = useState(''); //   
  const [placeType, setPlaceType] = useState('Landmark'); //  
  const [stayTime, setStayTime] = useState('1h'); //  
  const [uploadedImage, setUploadedImage] = useState(null); //  
  const [imagePreview, setImagePreview] = useState(''); //   URL
  const [mapLoaded, setMapLoaded] = useState(false);
  const [customAddress, setCustomAddress] = useState(''); //    
  const [originalAddress, setOriginalAddress] = useState(''); //  API   
  const [searchQuery, setSearchQuery] = useState(''); //   
  const mapRef = useRef(null);
  const markerRef = useRef(null);
  const fileInputRef = useRef(null);

  //  
  const searchLocationByQuery = async () => {
    if (!searchQuery.trim() || !mapRef.current) return;

    try {
      //  
      const data = await searchNaverPlace(searchQuery.trim());

      if (!data || data.length === 0) {
        alert('  .');
        return;
      }
      console.log(data[0]);

      const place = data[0];
      const lat = place.lat
      const lng = place.lng
      const position = new window.naver.maps.LatLng(lat, lng);

      setPlaceName(place.name);

      const finalAddress = place.address
      setCustomAddress(finalAddress);
      setOriginalAddress(finalAddress);

      //  
      mapRef.current.setCenter(position);

      //   
      removeCurrentMarker(markerRef);

      //     
      const marker = createAndRegisterMarker(position, mapRef, markerRef);

      //  
      await openInfoWindow({
        map: mapRef.current,
        marker,
        lat: lat,
        lng: lng,
        address: searchQuery.trim()
      });

      //   
      updateMarkerInfo(setMarkerInfo, lat, lng, searchQuery.trim());
      setShowInput(true);
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    }
  };

  //    
  const searchAddressAndUpdateMarker = () => {
    if (!customAddress.trim() || !mapRef.current) return;

    //      
    window.naver.maps.Service.geocode({
      query: customAddress
    }, async (status, response) => {
      if (status !== window.naver.maps.Service.Status.OK) {
        alert('    .   .');
        return;
      }

      //    
      if (response.v2.meta.totalCount === 0) {
        alert('  .   .');
        return;
      }

      //     
      const firstItem = response.v2.addresses[0];
      const position = new window.naver.maps.LatLng(firstItem.y, firstItem.x);
      
      //  
      mapRef.current.setCenter(position);
      
      //    ( )
      removeCurrentMarker(markerRef);

      //      ( )
      const marker = createAndRegisterMarker(position, mapRef, markerRef);

      const fullAddress = getAddress(firstItem)

      //   ( )
      await openInfoWindow({
        map: mapRef.current,
        marker,
        lat: position.lat(),
        lng: position.lng(),
        address: fullAddress
      });

      //     ( )
      updateMarkerInfo(setMarkerInfo, position.lat(), position.lng(), fullAddress);
      
      //     customAddress  
    });
  };

  //          ()
  //      Enter     

  //   
  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      //    ( 5MB )
      if (file.size > 5 * 1024 * 1024) {
        alert('  5MB  .');
        return;
      }

      //   
      if (!file.type.match('image.*')) {
        alert('   .');
        return;
      }

      //  Base64 
      const reader = new FileReader();
      reader.onload = (event) => {
        setUploadedImage(event.target.result); // Base64  
        setImagePreview(event.target.result); //  URL 
      };
      reader.readAsDataURL(file);
    }
  };

  //     
  const triggerFileInput = () => {
    fileInputRef.current.click();
  };

  //    (: " ..." -> "")
  const extractLocation = (address) => {
    if (!address) return '';
    
    // /  (,  )
    const cityMatch = address.match(/([^\s]+|[^\s]+|[^\s]+|[^\s]+)/);
    if (cityMatch) {
      // "", ""   
      return cityMatch[0].replace(/||/, '').trim();
    }
    return '';
  };
  
  //    Enter      
  const handleAddressKeyPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      searchAddressAndUpdateMarker();
    }
  };
  
  //    Enter      
  const handleSearchKeyPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      searchLocationByQuery();
    }
  };

  //  localStorage 
  const saveMarkerToLocalStorage = () => {
    if (!markerInfo) return;
    
    //   
    const name = placeName.trim();
    if (!name) {
      alert('  .');
      return;
    }
    
    //  API     
    //        
    const finalAddress = originalAddress.trim() || markerInfo.address;
    
    //   (/) 
    const location = extractLocation(finalAddress);
    
    //   ID 
    const id = new Date().getTime();
    
    const markerObj = {
      id: id,
      name: name,
      description: placeDescription, //   
      address: finalAddress,
      lat: markerInfo.lat,
      lng: markerInfo.lng,
      img: uploadedImage || defaultImage, //     
      location: location, //   (,  )
      type: placeType, //  
      stay: stayTime, //  
      popular: false, //    
      savedDate: new Date().toISOString(), //  
      favorite: false,
    };

    // localStorage   
    let markers = JSON.parse(localStorage.getItem("markers")) || [];
    markers.push(markerObj);

    // localStorage 
    localStorage.setItem("markers", JSON.stringify(markers));

    //      
    alert(' ! Saved  .');
    setPlaceName('');
    setPlaceDescription('');
    setPlaceType('Landmark');
    setStayTime('1h');
    setUploadedImage(null);
    setImagePreview('');
    setSearchQuery('');
    setCustomAddress('');
    setOriginalAddress('');
    setShowInput(false);
  };

  //     API 
  useEffect(() => {
    const checkNaverMapsLoaded = () => {
      // window.naver    
      if (window.naver && window.naver.maps) {
        console.log('  API  !');
        setMapLoaded(true); //  API   
      } else {
        console.error('  API   . window.naver:', window.naver);
        // 0.5   
        setTimeout(checkNaverMapsLoaded, 500);
      }
    };
    
    //  
    checkNaverMapsLoaded();
    
    //    
    return () => {
      if (markerRef.current) {
        markerRef.current.setMap(null);
      }
    };
  }, []);
  
  //   
  useEffect(() => {
    //  API    
    if (!mapLoaded) return;
    
    console.log('  ...');
    
    try {
      // 1.   
      const mapElement = document.getElementById('map');
      if (!mapElement) {
        console.error('  DOM    .');
        return;
      }
      
      // 2.    ()
      const defaultPosition = new window.naver.maps.LatLng(37.549186395087, 127.07505567644);
      
      // 3.   
      const mapOptions = {
        center: defaultPosition,
        zoom: 17,
        zoomControl: true,
        zoomControlOptions: {
          position: window.naver.maps.Position.TOP_RIGHT
        },
        mapTypeId: window.naver.maps.MapTypeId.NORMAL,
        mapTypeControl: true,
      };
      
      // 4.   
      console.log('   ...');
      const map = new window.naver.maps.Map(mapElement, mapOptions);
      mapRef.current = map;
      console.log('   !');
      
      // 5.   
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
          const { latitude, longitude } = position.coords;
          const currentPosition = new window.naver.maps.LatLng(latitude, longitude);
          
          //    
          map.setCenter(currentPosition);
          
          //    
          new window.naver.maps.Marker({
            position: currentPosition,
            map: map,
            icon: {
              content: '<div style="width: 16px; height: 16px; background-color: blue; border-radius: 50%; border: 2px solid white;"></div>',
              anchor: new window.naver.maps.Point(8, 8)
            }
          });
          
          console.log('   :', latitude, longitude);
        }, error => {
          console.warn('    :', error);
        });
      }
      
      // 6.     
      window.naver.maps.Event.addListener(map, 'click', function(e) {
        //   
        const clickedPosition = e.coord;
        
        //   
        removeCurrentMarker(markerRef)
        
        //   
        // const marker = createAndRegisterMarker(clickedPosition, map, markerRef)
        const marker = new window.naver.maps.Marker({
          position: clickedPosition,
          map: map
        });
        markerRef.current = marker;
        
        //      (Reverse Geocoding)
        window.naver.maps.Service.reverseGeocode({
          coords: clickedPosition,
          orders: [window.naver.maps.Service.OrderType.ADDR, window.naver.maps.Service.OrderType.ROAD_ADDR].join(',')
        }, async function(status, response) {
          if (status !== window.naver.maps.Service.Status.OK) {
            console.error('   ');
            return;
          }
          
          //   
          const address = getAddress(response.v2.address);
          
          //   ( )
          await openInfoWindow({
            map,
            marker,
            lat: clickedPosition.lat(),
            lng: clickedPosition.lng(),
            address
          });
          
          //   
          const markerData = {
            lat: clickedPosition.lat(),
            lng: clickedPosition.lng(),
            address: address
          };
          
          setMarkerInfo(markerData);
          setCustomAddress(address); //      
          setShowInput(true);
        });
      });
      
      console.log('  !');
    } catch (error) {
      console.error('    :', error);
    }
  }, [mapLoaded]); // mapLoaded    

  return (
    <div className="map-page">
      {!mapLoaded && (
        <div className="loading-message" style={{ textAlign: 'center', padding: '20px' }}>
          <p>  API ...</p>
        </div>
      )}
      
      {/*    */}
      <div className="search-container">
        <div className="search-box">
          <input 
            type="text" 
            className="search-input" 
            placeholder=" " 
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            onKeyPress={handleSearchKeyPress}
          />
          <button 
            className="search-button" 
            onClick={searchLocationByQuery}
          >
            
          </button>
        </div>
      </div>

      <div className="map-container">
        <div id="map" style={{ width: '100%', height: '600px', border: '1px solid #ddd' }}></div>
      </div>
      
      {showInput && markerInfo && (
        <div className="marker-info">
          <h3>  </h3>
          <div className="input-group">
            <label htmlFor="address"><strong>:</strong></label>
            <div className="address-input-container">
              <input 
                type="text" 
                id="address" 
                value={customAddress} 
                onChange={(e) => setCustomAddress(e.target.value)}
                onKeyPress={handleAddressKeyPress}
                placeholder="  Enter    "
              />
              <button 
                type="button" 
                className="search-address-btn"
                onClick={searchAddressAndUpdateMarker}
                title=" "
              >
                
              </button>
            </div>
          </div>
          <p><strong>:</strong> {markerInfo.lat.toFixed(6)}, <strong>:</strong> {markerInfo.lng.toFixed(6)}</p>
          
          {/*    */}
          <div className="image-upload-section">
            <div className="image-preview-container" onClick={triggerFileInput}>
              {imagePreview ? (
                <img src={imagePreview} alt="  " className="image-preview" />
              ) : (
                <div className="upload-placeholder">
                  <span></span>
                  <p> </p>
                </div>
              )}
            </div>
            <input 
              type="file" 
              accept="image/*" 
              onChange={handleImageUpload} 
              ref={fileInputRef} 
              style={{ display: 'none' }} 
            />
          </div>
          
          {/*     */}
          <div className="form-container">
            <div className="input-group">
              <label htmlFor="placeName"> :</label>
              <input 
                type="text" 
                id="placeName" 
                value={placeName} 
                onChange={(e) => setPlaceName(e.target.value)} 
                placeholder="  "
              />
            </div>

            <div className="input-group">
              <label htmlFor="placeDescription"> :</label>
              <textarea
                id="placeDescription"
                value={placeDescription}
                onChange={(e) => setPlaceDescription(e.target.value)}
                placeholder="   "
                rows="3"
                className="description-textarea"
              ></textarea>
            </div>
            
            <div className="input-group">
              <label htmlFor="placeType"> :</label>
              <select 
                id="placeType" 
                value={placeType}
                onChange={(e) => setPlaceType(e.target.value)}
              >
                <option value="Landmark"></option>
                <option value="Restaurant"></option>
                <option value="Cafe"></option>
                <option value="Shopping"></option>
                <option value="Culture">/</option>
                <option value="Nature">/</option>
                <option value="Entertainment"></option>
                <option value="Accommodation"></option>
              </select>
            </div>
            
            <div className="input-group">
              <label htmlFor="stayTime"> :</label>
              <select 
                id="stayTime" 
                value={stayTime}
                onChange={(e) => setStayTime(e.target.value)}
              >
                <option value="0.5h">30</option>
                <option value="1h">1</option>
                <option value="1.5h">1 30</option>
                <option value="2h">2</option>
                <option value="23h">2-3</option>
                <option value="34h">3-4</option>
                <option value="4h+">4 </option>
              </select>
            </div>
          </div>
          
          <button onClick={saveMarkerToLocalStorage} className="save-button">  </button>
        </div>
      )}
      
      <div className="map-guide">
        <p className="guide-text">     .</p>
        <p className="guide-text">  <strong>Saved</strong>    .</p>
      </div>
    </div>
  );
}

export default Map;