import React, { useEffect, useState, useRef } from 'react';
import './Modal.css';

export default function PlanModal({ visible, onClose, onCreate, planName, setPlanName, description, setDescription, uploadedImage, setUploadedImage, imagePreview, setImagePreview }) {
  const fileInputRef = useRef(null);

  if (!visible) return null;

  //     
  const triggerFileInput = () => {
    fileInputRef.current.click();
  };

  //   
  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      //    ( 5MB )
      if (file.size > 5 * 1024 * 1024) {
        alert('  5MB  .');
        return;
      }

      //   
      if (!file.type.match('image.*')) {
        alert('   .');
        return;
      }

      //  Base64 
      const reader = new FileReader();
      reader.onload = (event) => {
        setUploadedImage(event.target.result); // Base64  
        setImagePreview(event.target.result); //  URL 
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal" onClick={e => e.stopPropagation()}>
        {/* Header */}
        <div className="modal-header">
          <h2>Add new plan</h2>
          <button className="modal-close" onClick={onClose}>
            
          </button>
        </div>

        {/* Body */}
        <div className="modal-body">
          <label className="modal-label">Name</label>
          <div className="modal-input-group">
            <input
              type="text"
              placeholder=""
              value={planName}
              onChange={e => setPlanName(e.target.value)}
            />
          </div>

          <label className="modal-label">Descript</label>
          <textarea
            rows="4"
            placeholder=""
            value={description}
            onChange={e => setDescription(e.target.value)}
          />

          {/*    */}
          <div className="plan_image-upload-section">
            <div className="plan_image-preview-container" onClick={triggerFileInput}>
              {imagePreview ? (
                <img src={imagePreview} alt="  " className="plan_image-preview" />
              ) : (
                <div className="plan_upload-placeholder">
                  <span></span>
                  <p> </p>
                </div>
              )}
            </div>
            <input 
              type="file" 
              accept="image/*" 
              onChange={handleImageUpload} 
              ref={fileInputRef} 
              style={{ display: 'none' }} 
            />
          </div>
        </div>

        {/* Footer */}
        <div className="modal-footer">
          <button className="btn-create" onClick={onCreate}>
            
          </button>
        </div>
      </div>
    </div>
  );
}
